				 <i class="bi bi-arrow-right-circle-fill"></i>
			</button>
		</div>
		<!--  -->
		<main-menu v-show="isShowMainMenu" @close="isShowMainMenu = false"></main-menu>
		<!--  -->
		<modal v-show="showModal == 'setName'" @close="showModal = false">
			<div slot="header">
				
			</div>
